import coreRules from './databases/CoreRules';
import { armies } from './databases/ArmyRules';
import React, { useState, useEffect } from 'react';
import Sidebar from './components/Sidebar';
import Page from './components/Page';
import ArmyData from './components/ArmyData';
import { BsArrowRight } from 'react-icons/bs';

function App() {
  // The App's state, which let's the app know which page to mount.
  const [currentPage, setCurrentPage] = useState('Home');
  // Whenever the state updates (i.e. user clicks a page link), refresh the app to apply the new Page component.
  useEffect(() => {}, [currentPage]);
  return (
    <div>
      {/* Mount the Sidebar Menu component. */}
      <Sidebar setPage={setCurrentPage} />
      {/* If the currentPage state matches one of the Page components below, mount that Page component. */}
      {/* Home page */}
      {currentPage === 'Home' ? (
        <Page title='Welcome!'>
          <h3 className='display-4 mb-3'>Warmaster Pocket</h3>
          <p>It's a pocket-sized app to help you play Warmaster.</p>
          <p>95% of the original Warmaster Revolution rules have been preserved in this version, however all the rules have been rewritten to be easier to read.</p>
          <p>This is not a replacement for the Warmaster Revolution rules.</p>
          <p>
            Let me know what you think via the{' '}
            <a href='https://discord.gg/pB2uR7kcvv' target='_blank'>
              Warmaster Revolution discord!
            </a>
          </p>
        </Page>
      ) : (
        ''
      )}
      {/* Each rules page */}
      {coreRules.map((e) =>
        currentPage === e.name ? (
          <Page title={e.name}>
            {/* Import the rules for each chapter */}
            {coreRules.map((e, index) =>
              currentPage === e.name ? (
                <>
                  {e.text}
                  {/* Create a chapter footer with a Next Chapter button */}
                  <hr />
                  {index < coreRules.length - 1 ? (
                    <button onClick={() => setCurrentPage(coreRules[index + 1].name)}>
                      Next Chapter: {coreRules[index + 1].name} <BsArrowRight />
                    </button>
                  ) : (
                    <button onClick={() => setCurrentPage('Faq')}>
                      Check out the FAQ <BsArrowRight />
                    </button>
                  )}
                </>
              ) : (
                ''
              )
            )}
          </Page>
        ) : (
          ''
        )
      )}
      {/* Each army page */}
      {armies.map((e) =>
        currentPage === e.name ? (
          <Page title='Army List'>
            <h3 className='display-4 mb-3'>{e.name}</h3>
            <p>This army contains the following options:</p>
            <ArmyData army={e} />
          </Page>
        ) : (
          ''
        )
      )}
      {/* FAQ page */}
      {currentPage === 'Faq' ? (
        <Page title='FAQ'>
          <h3 className='display-4 mb-3'>Frequently Asked Questions</h3>
          <p>This page will be used for all rules clarifications.</p>
          <p>
            If you have any suggestions or questions, post it to the{' '}
            <a href='https://discord.gg/pB2uR7kcvv' target='_blank'>
              Warmaster Revolution discord!
            </a>
          </p>
        </Page>
      ) : (
        ''
      )}
      {/* Credits page */}
      {currentPage === 'Credits' ? (
        <Page title='Credits'>
          <h3 className='display-4 mb-3'>Thank You</h3>
          <p>Warmaster Pocket is a fan-made edit of the Warmaster Revolution tabletop rules.</p>
          <p>All rules writing and app development was done by Calvin St.</p>
          <p>Icons from game-icons.net under CC BY 3.0 â thanks to the talented artists: sbed, Lorc, Zeromancer, and Delapouite.</p>
          <p>All game design was done by the Warmaster Rules Committee â this is an unofficial fan-edit of their fantastic work.</p>
          <p>The original game design for Warmaster was written by Rick Priestley and published by Games Workshop.</p>
          <p>This website is completely unofficial and in no way endorsed by Games Workshop Limited.</p>
        </Page>
      ) : (
        ''
      )}
    </div>
  );
}

export default App;
